import React from 'react';
import Select from 'react-select';
import results from './results.json';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { average } from 'simple-statistics';

type CommuteTimes = typeof results;

interface CommuteTimePerStation {
  [station: string]: CommuteTimes;
};

interface CommuteTimePerHospital {
  hospital: string;
  transit: string;
  driving: string;
};

interface Comparison {
  station: string,
  transit: number,
  driving: number
};

const getTubeStationOptions = (commuteTimes: CommuteTimes) =>
  Array.from(
    new Set(
      commuteTimes
        .map(t => t.origin)
    )
  )
    .map(t => ({
      value: t,
      label: t
    }));

interface Props {
  tubeStations: string[]
};

interface State {
  commuteTimesPerStation?: CommuteTimePerStation;
}

export default class Compare extends React.PureComponent<{}, State> {

  public state: State = {}

  private onSelectTubeStation(tubeStations: string[]) {
    const commuteTimesPerStation: CommuteTimePerStation = {};

    for (const tubeStation of tubeStations) {
      const commuteTimes = results.filter(r => r.origin === tubeStation);
      commuteTimesPerStation[tubeStation] = commuteTimes;
    }

    this.setState({ commuteTimesPerStation })
  }

  private renderCommuteTimes(commuteTimesPerStation: CommuteTimePerStation) {
    return (
      Object.keys(commuteTimesPerStation).map((station, index) => (
        <div key={index}>
          <h2>{station}</h2>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Hospital</TableCell>
                <TableCell>Public Transport</TableCell>
                <TableCell>Driving</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {this.getCommutesTimePerHospital(commuteTimesPerStation[station]).map((h, index) => (
                <TableRow key={index}>
                  <TableCell>{h.hospital}</TableCell>
                  <TableCell>{h.transit}</TableCell>
                  <TableCell>{h.driving}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      ))
    );
  }

  private getCommutesTimePerHospital(commuteTimes: CommuteTimes): CommuteTimePerHospital[] {
    const hospitals = new Map<string, { [key: string]: string }>();

    for (const commuteTime of commuteTimes) {
      const hospital = hospitals.has(commuteTime.destination) ? hospitals.get(commuteTime.destination) : {}
      hospital[commuteTime.transitMode] = commuteTime.duration;
      hospitals.set(commuteTime.destination, hospital);
    }

    const commuteTimesPerHospital: CommuteTimePerHospital[] = [];

    for (const hospital of hospitals.keys()) {
      commuteTimesPerHospital.push({
        hospital,
        transit: hospitals.get(hospital)['transit'],
        driving: hospitals.get(hospital)['driving']
      });
    }

    return commuteTimesPerHospital;
  }

  private renderComparison(commuteTimesPerStation: CommuteTimePerStation) {
    return (
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Station</TableCell>
            <TableCell>Public Transport</TableCell>
            <TableCell>Driving</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {this.getComparison(commuteTimesPerStation).map((c, index) => (
            <TableRow key={index}>
              <TableCell>{c.station}</TableCell>
              <TableCell>{c.transit}</TableCell>
              <TableCell>{c.driving}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    );
  }

  private getComparison(commuteTimesPerStation: CommuteTimePerStation) {
    const comparison: Comparison[] = [];

    for (const station in commuteTimesPerStation) {
      const transit = Math.round(average(
        commuteTimesPerStation[station]
          .filter(t => t.transitMode === 'transit')
          .map(t => t.durationInMinutes)
      ));
      const driving = Math.round(average(
        commuteTimesPerStation[station]
          .filter(t => t.transitMode === 'driving')
          .map(t => t.durationInMinutes)
      ));

      comparison.push({ station, transit, driving });
    }

    return comparison;
  }

  public render() {
    return (
      <>
        <h1>Compare tube stations</h1>
        <Select
          options={getTubeStationOptions(results)}
          isMulti
          onChange={selected =>
            this.onSelectTubeStation(
              (selected as { label: string, value: string }[]).map(s => s.value))}
        />

        {this.state.commuteTimesPerStation && this.renderComparison(this.state.commuteTimesPerStation)}
        {this.state.commuteTimesPerStation && this.renderCommuteTimes(this.state.commuteTimesPerStation)}
      </>);
  }
};
